@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

section i {
  -webkit-animation: rotating 0.7s linear infinite;
          animation: rotating 0.7s linear infinite;
  width: 100%;
  text-align: center;

  margin-top: 20px;
  font-size: 30px;
}

.MovieOption {
  position: -webkit-sticky;
  position: sticky;
  top: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 10px;
  background-color: #d6ccff;
}
.MovieOption h5 {
  font-size: 30px;

  margin-right: 30px;
  font-family: "Do Hyeon", sans-serif;
}
.MovieOption .MovieOptionSelect {
  width: 30%;
  height: 40px;
  margin-right: 5px;
  padding: 3px;

  font-size: 20px;
