.background {
  z-index: -1;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
}
.DetailContainer {
  display: flex;
  justify-content: space-between;
}

.DetailContainer .DetailPoster,
.DetailElement {
  height: 80%;
  margin-top: 50px;
  z-index: 1;
}
.DetailContainer .whiteContainer {
  z-index: -1;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: 60%;
}

.DetailContainer .DetailPoster {
  position: relative;
  height: 80%;
  width: 30%;
  max-height: 80%;
}

.DetailContainer .DetailPoster img {
  width: 100%;
  max-height: 100%;
}

.DetailContainer .DetailElement {
  position: relative;
  right: 10px;
  width: 65%;
}
.DetailContainer .DetailElement .DetailTitle {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 50px;
}
.DetailContainer .DetailElement .DetailTitle h1 {
  font-size: 80px;
  margin-right: 50px;
}
.DetailContainer .DetailElement .DetailTitle h5 {
  font-size: 30px;
}

.DetailContainer .DetailElement .Rating {
  font-size: 30px;
  margin-bottom: 20px;
}

.DetailContainer .DetailElement .genresArray {
  display: flex;
  list-style-type: none;

  flex-wrap: wrap;
}
.DetailContainer .DetailElement .genresArray li {
  margin-right: 20px;
  font-size: 20px;
}
.DetailContainer .DetailElement .description {
  font-size: 30px;
  padding: 30px;
}

@media screen and (max-width: 1200px) {
  .DetailContainer .DetailElement .DetailTitle h1 {
    font-size: 50px;
    margin-right: 30px;
  }
}

@media screen and (max-width: 720px) {
  .DetailContainer .DetailElement .DetailTitle h1 {
    font-size: 30px;
  }
  .DetailContainer .DetailElement .DetailTitle h5 {
    font-size: 20px;
  }

  .DetailContainer {
    flex-direction: column;
    align-items: center;
  }
  .DetailContainer .DetailPoster {
    position: static;
    position: initial;
    height: 80%;
    width: 50%;
    max-height: 80%;
  }
  .DetailContainer .DetailElement {
    display: flex;
    flex-direction: column;
  }
  .DetailContainer .DetailElement .genresArray {
    width: 70%;
  }

  .DetailContainer .DetailPoster img {
    width: 100%;
    max-height: 100%;
  }
}

* {
