.Banner {
  z-index: 1;
  background-color: #706dca;
  width: 100%;

  display: flex;
  flex-direction: row;

  justify-content: center;
  align-items: center;
}
.Banner h1 {
  font-family: "Do Hyeon", sans-serif;
  font-weight: 10;
  margin-right: 30px;
  color: #ffffff;
}

.Banner .navigationBar {
  background-color: #dfe6e9;

  padding: 5px;

  height: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Banner .navigationBar a {
  color: black;
  text-decoration: none;

  font-size: 15px;
  padding: 1px 7px;
}
.Banner .navigationBar a:not(:last-child) {
  padding: 5px;
  border-right: 1px black solid;
}

